@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.main-nav {
  position: relative;
  min-height: 55px;
  color: #ffffff;
  font-size: 18px;
  line-height: 26px;
}
.main-nav--close .main-nav__toggle::before,
.main-nav--close .main-nav__toggle::after {
  position: absolute;
  right: 0;
  left: 0;
  top: 16px;
  margin: 0 auto;
  display: block;
  width: 23px;
  height: 4px;
  content: "";
  background-color: #81b3d2;
  box-shadow: 0 9px 0 0 #81b3d2, 0 -9px 0 0 #81b3d2;
}
.main-nav--open {
  z-index: 2;
}
.main-nav--open .main-nav__toggle::before,
.main-nav--open .main-nav__toggle::after {
  position: absolute;
  right: 0;
  left: 0;
  top: 16px;
  margin: 0 auto;
  transform: rotate(45deg);
  display: block;
  width: 28px;
  height: 4px;
  content: "";
  background-color: #ffffff;
}
.main-nav--open .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__wrapper {
  position: relative;
  z-index: 2;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__wrapper {
  position: absolute;
  width: 100%;
}
.main-nav__toggle {
  position: absolute;
  z-index: 3;
  top: 10px;
  right: 13px;
  width: 35px;
  height: 35px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__list-item {
  margin: 0;
  padding: 0;
  font-weight: 700;
  background-color: #81b3d2;
  border-bottom: 1px solid #678fa8;
  border-top: 1px solid #9ac2db;
  text-align: center;
}
.main-nav__list-item:hover {
  background-color: #669ec0;
}
.main-nav__list-item:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.main-nav__list-item:first-child {
  border-top: none;
}
.main-nav__list-item:last-child {
  position: relative;
  border-bottom: none;
}
.main-nav__list-item:last-child::after {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  content: "";
  height: 7px;
  background-image: linear-gradient(transparent, rgba(0, 0, 0, 0.2));
}
.main-nav__link {
  display: block;
  width: 100%;
  padding: 17px 0 12px;
  color: inherit;
  text-decoration: none;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .main-nav--close .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    display: grid;
    grid-template-columns: 1fr 1fr 155px 1fr 1fr;
  }
  .main-nav__link {
    padding: 8px 0;
  }
  .main-nav__wrapper {
    position: static;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list-item {
    padding: 10px;
    font-size: 14px;
    line-height: 21px;
    color: #000000;
    background-color: #ffffff;
    border: none;
  }
  .main-nav__list-item:hover {
    background-color: #ffffff;
    color: #81b3d2;
  }
  .main-nav__list-item:active {
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.3);
  }
  .main-nav__list-item--current {
    color: #766357;
  }
  .main-nav__list-item:nth-child(4n + 3) {
    grid-column-start: 4;
  }
  .main-nav__list-item:last-child::after {
    content: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    grid-template-columns: 1fr 1fr 240px 1fr 1fr;
  }
}
.header {
  position: relative;
}
.header__logo-small {
  position: absolute;
  left: 50%;
  z-index: 1;
  transform: translateX(-50%);
  width: 185px;
  height: 96px;
}
.advantages {
  text-align: center;
  text-transform: uppercase;
}
.advantages__title {
  margin-bottom: 35px;
  padding: 0 15px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.advantages__item-title {
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 26px;
}
.advantages__item-description {
  margin: 18px 0;
}
.advantages__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.advantages__break-line {
  display: none;
}
.advantages__item-text-content {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}
.advantages__item--wide {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  background-color: #81b3d2;
}
.advantages__item--wide .advantages__item-text-content {
  padding: 23px 0 18px;
}
.advantages__item--wide:first-child {
  position: relative;
  margin-bottom: 15px;
}
.advantages__item--wide:first-child .advantages__item-text-content {
  padding-top: 33px;
}
.advantages__item--wide:first-child::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 15px;
  content: "";
  clip-path: polygon(0 0, 50% 100%, 100% 0);
  background-color: #ffffff;
}
.advantages__item--with-background {
  background-color: #eeeeee;
  border-bottom: 1px solid #d6d6d6;
}
.advantages__item--with-background:last-child {
  border-bottom: none;
}
.advantages__item--with-background .advantages__item-text-content {
  padding-top: 27px;
  width: 250px;
  min-height: 140px;
}
.advantages__item--with-background .advantages__item-title {
  margin-top: 0;
}
.advantages__item--icons-decorated {
  box-sizing: border-box;
  width: 87.5%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 5px;
  padding-left: 47px;
  text-align: start;
  background-repeat: no-repeat;
  background-position: 0 10px;
}
.advantages__item--icons-decorated .advantages__item-text-content {
  width: 250px;
  margin-left: 0;
  margin-right: 0;
}
.advantages__item--icons-decorated .advantages__item-description {
  margin: 16px 0;
}
.advantages__item--icons-decorated .advantages__item-title {
  margin-top: 10px;
}
.advantages__item--house {
  padding-left: 45px;
  background-image: url("../img/background-icons/icon-house.svg");
  background-size: 39px 37px;
}
.advantages__item--food {
  padding-left: 50px;
  background-image: url("../img/background-icons/icon-burger.svg");
  background-size: 40px 35px;
}
.advantages__item--gift {
  margin-bottom: 51px;
  padding-left: 43px;
  background-image: url("../img/background-icons/icon-gift.svg");
  background-size: 32px 38px;
  background-position: 3px 7px;
}
.advantages__item-picture {
  display: block;
  width: 100vw;
  height: 260px;
  object-fit: cover;
}
@media (min-width: 768px) {
  .advantages__item {
    min-height: 256px;
  }
  .advantages__item--wide {
    display: grid;
    grid-template-columns: minmax(256px, 1fr) 2fr;
    grid-column: span 3;
    align-items: start;
  }
  .advantages__item--wide .advantages__item-text-content {
    padding: 0;
  }
  .advantages__item--wide:first-child {
    margin-bottom: 0;
  }
  .advantages__item--wide:first-child .advantages__item-text-content {
    padding-top: 0;
  }
  .advantages__item--wide:first-child::before {
    content: none;
  }
  .advantages__item--wide-reverse {
    grid-template-columns: 2fr minmax(256px, 1fr);
  }
  .advantages__item--wide-reverse .advantages__item-text-content {
    order: 2;
  }
  .advantages__item--with-background {
    border: none;
  }
  .advantages__item--with-background .advantages__item-text-content {
    padding-top: 0;
    width: 176px;
  }
  .advantages__item--with-background .advantages__item-title {
    margin-top: 43px;
  }
  .advantages__item--icons-decorated {
    width: auto;
    min-height: 330px;
    padding-top: 130px;
    padding-left: 0;
    text-align: center;
    background-position: center 60px;
  }
  .advantages__item--icons-decorated .advantages__item-text-content {
    width: 195px;
  }
  .advantages__item--house {
    background-size: 75px 72px;
  }
  .advantages__item--food {
    background-size: 76px 68px;
  }
  .advantages__item--gift {
    background-size: 64px 75px;
    margin-bottom: 0;
  }
  .advantages__item-description {
    margin-top: 25px;
  }
  .advantages__item-title {
    margin-top: 10px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 21px;
  }
  .advantages__item-text-content {
    width: 195px;
    margin: 0 auto;
    margin-top: 32px;
  }
  .advantages__item-picture {
    object-fit: cover;
    width: 100%;
    height: 256px;
  }
  .advantages__item-picture--scale-position {
    object-position: top calc(0px + 10%) right 0;
  }
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, minmax(256px, 1fr));
  }
  .advantages__title {
    margin-bottom: 55px;
  }
}
@media (min-width: 1200px) {
  .advantages__item--icons-decorated .advantages__item-text-content {
    width: 282px;
  }
  .advantages__item--with-background .advantages__item-text-content {
    width: 290px;
  }
  .advantages__item--with-background .advantages__item-title {
    margin-left: 70px;
    margin-right: 70px;
  }
  .advantages__item--wide .advantages__item-text-content {
    margin-top: 40px;
  }
  .advantages__break-line {
    display: block;
  }
  .advantages__title {
    margin-bottom: 45px;
  }
  .advantages__item-text-content {
    width: 280px;
  }
  .advantages__item-title {
    margin-left: 35px;
    margin-right: 35px;
  }
}
.button {
  display: block;
  padding: 30px;
  padding-top: 18px;
  padding-bottom: 17px;
  font-family: inherit;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #81b3d2;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.control {
  display: flex;
  align-items: center;
  width: 224px;
  height: 46px;
}
.control__input {
  position: relative;
  margin: 0;
  appearance: none;
}
.control__input::before {
  display: block;
  content: "";
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
}
.control__input:hover::before {
  background-color: #e5e5e5;
}
.control__input:active::before {
  background-color: #d5d5d5;
}
.control__input:checked::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  display: block;
  width: 46px;
  height: 46px;
  background-image: url("../img/background-icons/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 22px 16px;
}
.control__input:active::after {
  opacity: 0.3;
}
.control__input:disabled::before {
  background-color: #fafafa;
}
.control__input:disabled::after {
  opacity: 0.2;
}
.control__input[type="radio"]::before {
  border-radius: 50%;
}
.control__input[type="radio"]:checked::before {
  background-image: none;
}
.control__input[type="radio"]:checked::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  margin: auto;
  content: "";
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
}
.control__label {
  margin-left: 16px;
  text-transform: none;
}
@media (min-width: 768px) {
  .control {
    width: auto;
  }
}
.copyright {
  text-transform: uppercase;
}
.copyright__link {
  text-decoration: none;
  color: inherit;
}
.copyright__link:hover {
  color: #81b3d2;
}
.copyright__link:active {
  color: #b3b3b3;
}
.copyright__text {
  margin-right: 9px;
}
.copyright__icon {
  vertical-align: middle;
}
.feedback {
  text-transform: uppercase;
  padding: 0 6.25%;
}
.feedback__title {
  position: relative;
  max-width: 435px;
  margin: 25px auto 52px;
  text-align: center;
  font-size: 14px;
  font-weight: 400;
}
.feedback__title::before {
  position: absolute;
  left: 50%;
  bottom: -30px;
  transform: translateX(-50%);
  content: "";
  width: 100vw;
  height: 1px;
  background-color: #e7e5e5;
}
.feedback__footnote {
  font-size: 12px;
}
.feedback__form-group {
  margin: 0;
  margin-top: 25px;
  border: none;
}
.feedback__form-group::before {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  content: "";
  width: 100vw;
  height: 1px;
  background-color: #e7e5e5;
}
.feedback__form-group:last-of-type::before {
  content: none;
}
.feedback__form-button {
  width: 100%;
  margin-top: 8px;
}
.feedback__button-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 280px;
  margin: 0 auto 25px;
}
@media (min-width: 768px) {
  .feedback {
    padding: 0 7.8%;
  }
  .feedback__title {
    margin-top: 20px;
    margin-bottom: 80px;
  }
  .feedback__title::before {
    content: none;
  }
  .feedback__form-group-wrapper {
    position: relative;
    display: flex;
  }
  .feedback__form-group {
    margin-top: 24px;
  }
  .feedback__form-group::before {
    content: none;
  }
  .feedback__footnote {
    font-size: 14px;
  }
  .feedback__form-button {
    width: 324px;
    margin: 0;
  }
  .feedback__button-container {
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .feedback {
    padding-left: 120px;
    padding-right: 120px;
  }
  .feedback__title {
    margin-bottom: 45px;
  }
  .feedback__button-container {
    width: 768px;
  }
  .feedback__form-button {
    width: 384px;
  }
}
.form-group {
  position: relative;
  padding: 0;
  padding-bottom: 40px;
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
}
.form-group--wrap {
  display: flex;
  flex-wrap: wrap;
}
.form-group--user-name .form-group__item {
  margin-bottom: 12px;
}
.form-group--user-contacts {
  padding-bottom: 35px;
}
.form-group--user-contacts .form-group__item {
  margin-bottom: 10px;
}
.form-group--impress,
.form-group--attractions {
  padding-bottom: 25px;
}
.form-group--impress .form-group__item,
.form-group--attractions .form-group__item {
  padding-right: 15px;
}
.form-group--attractions .form-group__title,
.form-group--emotions .form-group__title {
  margin-bottom: 20px;
}
.form-group__item {
  position: relative;
  min-width: 280px;
  flex-grow: 1;
  margin-bottom: 16px;
}
.form-group__item:last-child {
  margin-bottom: 0;
}
.form-group__item--tel .form-group__field,
.form-group__item--email .form-group__field {
  padding-right: 75px;
}
.form-group__item--tel::before {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 58px;
  height: 58px;
  content: "";
  background-color: #e6e6e6;
  background-image: url("../img/background-icons/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 18px 18px;
}
.form-group__item--email::before {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 58px;
  height: 58px;
  content: "";
  background-color: #e6e6e6;
  background-image: url("../img/background-icons/mail.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 12px;
}
.form-group__title {
  margin-bottom: 15px;
  text-align: center;
}
.form-group__field {
  display: block;
  box-sizing: border-box;
  width: 100%;
  padding: 16px;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: 700;
  background-color: #f2f2f2;
  border: none;
}
.form-group__field-label {
  margin-left: 15px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}
.form-group__textarea {
  box-sizing: border-box;
  width: 100%;
  height: 150px;
  padding: 16px;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  font-weight: 700;
  resize: none;
  border: none;
