    min-width: 200px;
    padding: 0;
    margin-right: 65px;
  }
  .form-group--impress .form-group__item:nth-of-type(3n) {
    margin-right: 0;
  }
  .form-group--impress .form-group__title {
    margin-bottom: 45px;
  }
  .form-group--emotions {
    margin-top: 70px;
    padding-left: 95px;
    padding-right: 95px;
  }
  .form-group--attractions,
  .form-group--impress,
  .form-group--emotions {
    border-top: 1px solid #dbdbdb;
  }
  .form-group--attractions .form-group__title,
  .form-group--impress .form-group__title,
  .form-group--emotions .form-group__title {
    padding-left: 65px;
    padding-right: 65px;
  }
}
.gallery {
  width: 280px;
  margin: 0 auto;
